.tree {
    display: flex;
    justify-content: center;
}
.node {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 20px;
}
.node.node--left {
    margin-right: 60px;
}
.node.node--right {
    margin-left: 60px;
}
.node:not(.node--root) .node__link {
    height: 20px;
    width: 1px;
    background-color: black;
}
.node__data {
    display: flex;
    width: 56px;
    height: 56px;
    border-radius: 56px;
    border: 2px solid gray;
    background-color: rgb(255, 255, 255);
    justify-content: center;
    align-items: center;
}

.node__children {
    display: flex;
    padding : 0 20px;
    border-top: 1px solid black;
}
.node__line {
    height: 20px;
    width: 1px;
    background-color: black;
}

.node__comparing {
    background-color: rgb(255, 0, 0);
}

.node__found {
    background-color: rgb(0, 255, 0);
}

.button_container {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
}

.button_container > *{
    margin: 10px 30px;
    width: 150px;
}

.utility {
    display: flex;
    margin-bottom: 20px;
    border-bottom: 1px solid black;
    border-left: 1px solid black;
    border-right : 1px solid black;
}
.checklist{
    width: 760px;
    max-height: 140px;
    overflow-y: auto;
    border-right: 1px solid black;
}
.history {
    width: 760px;
    max-height: 140px;
    overflow-y: auto;
    border-left: 1px solid black;
}
.temp {
    width: 400px;
    padding-top: 10px;
}
.status {
    height: 20px;
    padding: 20px;
    width: 360px;
    border-top: 1px solid black;
}
.temp .node__data{
    position: relative;
    margin-bottom: 10px;
    left: 50%;
    transform: translate(-50%,0);
}
input {
    text-align: center;
    width: 50px;
}
table {
    width: 100%;
    border-collapse: collapse;
}
th, td {
    padding : 10px 15px;
}
th {
    background-color: rgb(228, 228, 228);
    position: sticky;
    top: 0px;
}
button {
    background-color: rgb(228, 228, 228);
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

